.rs-code-block {
  background: #282c34;
  color: white;
  border-radius: 10px;
  position: relative;
  font-weight: 100;
}
.rs-code-block .langs {
  position: absolute;
  right: 5px;
  top: 5px;
  font-size: 0.15em;
  color: rgba(255, 255, 255, 0.4);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.rs-code-block code {
  word-break: break-word;
  white-space: pre-wrap;
  background: transparent;
}
.rs-code-block .line-highlight {
  position: absolute;
  width: 100%;
  margin-top: 13px;
  margin-left: -14px;
  background-color: rgba(255, 255, 255, 0.1);
}



